<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='readonly'
                       :showReturn='false'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>
    <codeLogic :readonly='readonly'></codeLogic>
  </div>
</template>

<script>
import {
  getCodeLogicList, addCodeLogic
  , detailCodeLogic, updateCodeLogic, deleteCodeLogic
} from '@/service/api/codeLogic.js'
import codeLogic from './modules/codeLogic'
export default {
  components: {
    codeLogic
  },
  props: {},
  data () {
    return {
      readonly: false,
      list: {}
    };
  },
  computed: {
  },
  created () { },
  watch: {},
  mounted () {
    this.readonly = this.$store.state.iconstatus
  },
  beforeDestroy () {
  },
  methods: {
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
        deleteCodeLogic({ uuid: this.$store.state.uuid }).then(res => {
          if (res.code === '200') {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.push({
              name: 'codeLogic'
            })
          }
        })
      })
    },
  },
};
</script>

